	module.exports = {"data":{"markdownRemark":{"html":"<h2>Definition of Mean</h2>\n<p>In statistics, the term <em>mean</em> refers to the ‘average’ value in a collection of data. You can find this value by adding together all of the numbers and then dividing by the total number of values in the collection.</p>\n<p>The Mean is the average value of a data set.</p>\n<p>The Mean value is calculated by adding each number in a set of data and dividing that total by the amount of numbers in the data set.  </p>\n<p><img src=\"https://github.com/tribalizzles/FreeCodeCamp-projects/blob/master/Mean_Calculation.png?raw=true\" alt=\"An example of a Mean calculation\"></p>\n<h3>Example</h3>\n<p>The mean of ( 5, 8, 10, 12, 15 ) is 8:</p>\n<p><em>To find the mean we would find the sum of all of these numbers (40) and divide it by the number of values in the collection (5).  40/5 is 8, which is the average value in the collection.</em></p>\n<h3>Median and Mode</h3>\n<p>Mean is often confused with <a href=\"https://en.wikipedia.org/wiki/Median\">median</a>, the value in the middle of the collection and <a href=\"https://guide.freecodecamp.org/mathematics/definition-of-mode\">mode</a>, the value that occurs most frequently in the collection.</p>\n<h3>More Information</h3>\n<ul>\n<li><a href = \"http://www.mathsisfun.com/definitions/mean.html\">Definition of a Mean</a></li>\n<li><a href = \"http://www.mathsisfun.com/mean.html\">How to calculate a Mean</a></li>\n</ul>","fields":{"slug":"/mathematics/definition-of-mean/"},"frontmatter":{"title":"Definition of Mean"}}},"pathContext":{"slug":"/mathematics/definition-of-mean/"}}

